<template>
  <div class="wrap-setting">
    <h3>설정</h3>
    <p class="txt-balloon">
      약속한 사용 시간을 설정해요.<br />
      “제한 없음”은 시간 설정을 하지 않아요.<br />
      시간이 다 되면 진행 중인 활동이 끝난 뒤<br />
      시간 완료 안내를 합니다.
    </p>
    <div class="timer">
      <h4>시간 설정</h4>
      <!-- <button class="btn-question active">?</button> -->
      <div class="txt-time">
        <span class="num">{{ time }}</span
        >분
      </div>
      <div class="slider">
        <button ref="silderBar" class="bar" />
        <div ref="silderFill" class="fill" />
        <input
          ref="slider"
          v-model="time"
          type="range"
          value="0"
          min="0"
          max="150"
        />
      </div>
      <div class="txt-limit">
        <p class="min">5분</p>
        <p class="max">150분</p>
        <div class="no-limit"><input type="checkbox" /> 제한 없음</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      per: 59 / 15,
      time: 0
    }
  },
  watch: {
    time() {
      this.$refs.silderBar.setAttribute(
        'style',
        `left:${this.per * this.time}px`
      )
      this.$refs.silderFill.setAttribute(
        'style',
        `width:${this.per * this.time}px`
      )
    }
  },
  methods: {}
}
</script>

<style lang="scss" scoped>
.wrap-setting {
  position: relative;
  height: 100%;
  background: url('~@/assets/images/main/parent/img_setting_balloon.svg')
    no-repeat right 216px;
  font-weight: 500;
  h3 {
    background: url('~@/assets/images/main/parent/ico_tit_setting.svg')
      no-repeat 0 5px;
    background-size: 28px;
    padding-left: 34px;
    font-weight: 500;
    vertical-align: bottom;
    color: $color3;
    span {
      display: inline-block;
      padding-left: 8px;
      font-size: 18px;
      font-weight: 400;
      color: $color9;
    }
  }
  .txt-balloon {
    position: absolute;
    top: 235px;
    right: 11px;
    font-size: 18px;
    font-weight: 400;
    text-align: center;
    line-height: 34px;
    color: $color6;
  }
}
.timer {
  position: relative;
  width: 590px;
  margin-top: 25px;
  h4 {
    width: 124px;
    height: 46px;
    background: #9a73ff;
    border-radius: 0 20px 20px 22px;
    font-weight: 400;
    text-align: center;
    line-height: 42px;
    color: $colorF;
  }
  .btn-question {
    position: absolute;
    width: 42px;
    height: 42px;
    top: 2px;
    left: 136px;
    background: $colorB;
    border-radius: 100%;
    font-family: 'tmoney-bold';
    font-size: 26px;
    color: $colorF;
    &.active {
      background: #50acdb;
    }
  }
  .txt-time {
    position: absolute;
    top: 8px;
    right: 0;
    font-size: 28px;
    font-weight: 500;
    color: $color3;
    .num {
      display: inline-block;
      width: 110px;
      height: 50px;
      border: 1px solid #50acdb;
      border-radius: 25px;
      margin-right: 8px;
      font-family: 'tmoney-bold';
      font-size: 30px;
      text-align: center;
      line-height: 46px;
      color: #ff4747;
    }
  }
  .slider {
    position: relative;
    width: 590px;
    height: 40px;
    margin-top: 25px;
    &::before {
      position: absolute;
      top: 14px;
      left: 0;
      content: '';
      display: block;
      width: 100%;
      height: 10px;
      background: #cfcfcf;
      border-radius: 50px;
      z-index: 1;
    }
    & > * {
      position: absolute;
    }
    .fill {
      top: 14px;
      left: 0;
      width: 0;
      height: 10px;
      background: #9a73ff;
      border-radius: 50px;
      z-index: 2;
    }
    .bar {
      @include boxShadow(4px, 1);
      top: -1px;
      left: 0;
      width: 40px;
      height: 40px;
      margin-left: -20px;
      border-radius: 100%;
      background: #9a73ff;
      transition: none;
      -webkit-transition: none;
      z-index: 3;
    }
    input[type='range'] {
      -webkit-appearance: none;
      height: 25px;
      top: 5px;
      left: 0;
      width: 590px;
      cursor: pointer;
      opacity: 0;
      z-index: 4;
      &::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 25px;
        height: 25px;
        background: #4caf50;
        cursor: pointer;
      }
      &::-moz-range-thumb {
        width: 25px;
        height: 25px;
        background: #4caf50;
        cursor: pointer;
      }
    }
  }
  .txt-limit {
    overflow: hidden;
    font-size: 24px;
    color: $color6;
    .min {
      float: left;
    }
    .max {
      float: right;
    }
    .no-limit {
      clear: both;
      padding-top: 26px;
      font-size: 20px;
      color: $color3;
      input {
        width: 40px;
        height: 40px;
        border: 1px solid $color9;
        margin-right: 10px;
      }
    }
  }
}
</style>
